body{
	background-size: 100% auto;
	background-color: #000000;
}

header{
    background-color: #222222FF;
    color : rgba(0, 255, 0, 255);
    height : 60px;
    font-size: 28px;
    overflow: hidden;
    font-family: Oxygen, sans-serif;

}
td{
    color : white;
    width : 20%;
    text-align: center;
    padding : 3px 0px 3px 0px;
}
tr:hover {
    cursor: pointer;
    border: 4mm ridge rgba(0, 109, 253, .7);
}


.center_box {
background-color: rgba(15, 183, 177, 0.4);
margin-left : 40% ;
width : 320px;
width : 320px;
margin-top :11%;
border: 0;
border-radius: 3px;

}

.words {
color : white;
margin : 5px
}
h1{
text-align: center}

.elem{
margin :  9px
}

.text_box{
width : 300px;
border: 0;
border-radius: 3px;
font-size: 18px;
font-family: Oxygen, sans-serif;
margin-top:5px;
}
.passwd {
-webkit-text-security: disc;
}

.login-btn {

    background-color: white;
    border: 0;
    border-radius: 3px;
    font-size: 18px;
    font-family: Oxygen, sans-serif;
    cursor: pointer;
    width : 100px; /* блок занимает ширину родителя, max-width её ограничивает */
    height : 30px;
    margin: 20px 0px 3px 3px;

}
.error_label{

    color : red;
    margin: 0px 0px 0px 40px;
}

.hello_btn{
display: inline-block;
height : 40px;
vertical-align: top;
}

.nav_btn{
     display: inline-block;
     max-width : 150px;
     height : 40px;
     margin : 3px 30px 3px 30px;
     overflow: hidden;
     font-size: 18px;
     text-align: center;
     color: #888888FF;
}

.nav_btn_color{
    color: #006DFDFF;
    font-weight: bolder;
    background-color: #303B44FF;
    border: 0;
    border-radius: 3px;
    width: 80px;
    height: 25px;
    padding: 5px 5px 5px 5px;
    cursor: pointer;
}

.bone {
    background-color: #22222FFF;
    border-radius: 2px;
    width : 800px;
    margin : 30px 20% 0px 20%;
}




